@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: rgb(97, 218, 251);
  outline: none !important;
}
*:focus,
*:active {
  outline: none !important;
}
body{
  background-color: #282c34;
}
.btn{
  background: none;
  border: 1px solid rgb(97, 218, 251);
  outline: none;
  padding: 10px 20px;
  border-radius: 5px;
//   width: fit-content;
  width: 100%;
  cursor: pointer;
  margin: 0 auto;
  &__mt10{
    margin-top: 10px;
  }
  &__m0auto{
    display: flex;
    margin: 0 auto;
  }
  &__dark{
    border: 1px solid black;
    color: black;
  }
}
.btn:hover{
  background: rgba(97, 218, 251, 0.1);
}
.ul{
    max-width: 90vw;
    width: fit-content;
    word-break: break-word;
    margin: 0 auto;
}
.input{
  padding: 10px 5px;
  outline: none;
  font-size: 17px;
  background: none;
  border: 1px solid rgb(97, 218, 251);
  width: 100%;
  text-align: left;
  display: block;
  border-radius: 5px;
}
.input:focus {outline: none !important;}
.input::-moz-focus-inner {border: 0px !important;}



.App{
    // width: 100vw;
    // background: #fff;
    max-width: 800px;
    margin: 0 auto;
}
.filteredItem{
    font-size: 15px;
    color: white;
}
.blacksquare{
    height: 50px;
    width: 50px;
    background: #000000;
}
.textarea{
    resize: none;
    color: black;
    font-size: 13px;
    width: 80%;
    height: 400px;
    margin: 0 auto;
    display: flex;
}