.ul{
    max-width: 90vw;
    // width: fit-content;
    word-break: break-word;
    margin: 20px auto 0;
    &__item{
        display: grid;
        grid-template-columns: 1fr 40px;
        border-bottom: 1px solid #f1f1f1;
        padding: 5px;
        text-align: center;
        &:first-child{
            border-top: 1px solid #f1f1f1;
        }
    }
}
.filterBlock{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    max-width: 200px;
    margin: 20px auto;
}
.button{
    background: none;
    border: 1px solid rgb(97, 218, 251);
    outline: none;
    padding: 10px 20px;
    border-radius: 5px;
    width: 100%;
    cursor: pointer;
    margin: 0 auto;
    &:hover{
        background: rgba(97, 218, 251, 0.1);
      }
  }
.input{
    padding: 10px 5px;
    outline: none;
    font-size: 17px;
    background: none;
    border: 1px solid rgb(97, 218, 251);
    width: 100%;
    text-align: left;
    display: block;
    border-radius: 5px;
  }