@import '../../Variables';
.navbar{
    display: grid;
    // grid-template-columns: repeat(2,1fr);
    grid-template-columns: 1fr;
    gap: 10px;
    padding: 10px;
}
.label{
    align-self: center;
    padding: 10px;
    font-size: 17px;
    & input[type='checkbox']{
        font-size: 17px;
        margin-right: 10px;
        cursor: pointer;
    }
    & input{
        font-size: 17px;
        margin-right: 10px;
    }
}
.input{
    background: none;
    border: none;
    width: 100%;
    cursor: text;
    // border: 1px solid white;
}
@media (min-width: 550px){
    .navbar{
        grid-template-columns: repeat(2,1fr);
        text-align: center;
    }
    .label{
        margin: 0 auto;
        & input{
            text-align: center;
        }
    }
}